import uniPopup from "../../components/uni-popup/uni-popup.vue"
export default {
	components: {
		uniPopup
	},
	data() {
		return {
			content:"",
			isshow: true,
		};
	},
	onLoad() {
		
	},
	onShow(){
		this.getNotice();
		this.login();
		
	},
	onPullDownRefresh() {
		setTimeout(function () {
			uni.stopPullDownRefresh();
		}, 1000);
	},
	methods: {
		getNotice(){
			var that =this;
			uni.request({
				url: 'http://106.13.239.202/index.php?c=main&m=getNotice',
				method: 'GET',
				header: {"content-type": "application/x-www-form-urlencoded"}, 
				data: {
				},
				success: res => {
					console.log(res);
					if(res.data.status == 'OK'){
						that.content = res.data.data[0].content;
					}else{
						uni.showToast({title:"获取公告失败", icon:"none"});
						
					}
						
				},
				fail: () => {
				
				},
				complete: () => {}
			});
		},
		toRepair() {
			uni.navigateTo({
				url: '/pages/repair/edit'
			});
		},
		toPayment() {
			uni.navigateTo({
				url: '/pages/payment/list'
			});
		},
		toRent() {
			uni.navigateTo({
				url: '/pages/rent/list'
			});
		},
		to() {
			uni.navigateTo({
				url: '/pages/payment/payment/payment'
			});
		},
		toTaxi() {
			uni.navigateTo({
				url: '/pages/taxi/taxi'
			});
		},
		toLogin() {
			uni.navigateTo({
				url: '/pages/taxi/taxi'
			});
		},
		isLogin(){
			uni.getStorage({
			    key: 'code',
			    success: function (res) {
					console.log(res);
			        if(!res.data){
						uni.navigateTo({
							url: '/pages/login/login'
						});
					}
						
					
			    }
			});
			
		}

		
		
	}
};
